*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}


.scene{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform-style: preserve-3d;
    position: relative;
    background: linear-gradient(-90deg, rgba(243,197,197,1) 0%, rgba(193,163,163,1) 100%);
    
}

.cuboid,.right,.left,.top,.bottom,.tray-top,.tray-bottom{
    /* background-color: rgba(0,0,0,0.2); */
    background-color:rgba(60,203,200,0.2);
    --width:100px;
    --height:100px;
    --depth:100px;
    position: absolute;
    height: var(--height);
    width: var(--width);
    transform:rotateX(24deg) rotateY(32deg);
    transform-style: preserve-3d;
}

.cuboid-side{
    /* background-color: rgba(0,0,0,0.2);
    border:1px solid rgba(0,0,0,0.5); */
    background-color: hsla(0,0%,50%,0.2);
    border: 1px solid hsla(0,0%,10%,0.2);
}

.cuboid-side:nth-child(1),
.cuboid-side:nth-child(2){
    --coeff:0.5;
    position: absolute;
    height: var(--height);
    width:var(--width);
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%) translateZ(calc(var(--depth) * var(--coeff)));
    
}
.cuboid-side:nth-child(2){
    --coeff:-0.5;
}

.cuboid-side:nth-child(3),
.cuboid-side:nth-child(4){
    --coeff:0.5;
    --rot:90deg;
    position: absolute;
    height:var(--height);
    width:var(--depth);
    top:50%;
    left:50%;
    transform:translate(-50%,-50%) rotateY(var(--rot)) translateZ(calc(var(--width) * var(--coeff)))
}
.cuboid-side:nth-child(4){
    --rot:-90deg;
}

.cuboid-side:nth-child(5),
.cuboid-side:nth-child(6){
    --coeff:0.5;
    --rot:90deg;
    position: absolute;
    height:var(--depth);
    width:var(--width);
    top:50%;
    left:50%;
    transform:translate(-50%,-50%) rotateX(var(--rot)) translateZ(calc(var(--height) * var(--coeff)));
}
.cuboid-side:nth-child(6){
    --rot:-90deg;
}

