.paper{
    width:150px;
    height:150px;
    /* border:1px solid black; */
    /* transform: rotateZ(14deg) rotateY(-48deg) rotateX(54deg) translateX(-99px) translateY(0px) translateZ(0px) scaleX(1) scaleY(1); //printedpaper */
    transform: rotateZ(15deg) rotateY(-44deg) rotateX(52deg) translateX(200px);
    /* transform: rotateZ(23deg) rotateY(50deg) rotateX(11deg) translateX(256px) translateY(-60px); */
    /* transform: rotateZ(15deg) rotateY(50deg) rotateX(-2deg) translateX(-28px) translateY(-245px); */
    background-color: rgb(241, 250, 245);
    cursor: pointer;
    transform-origin: center center;
    z-index:-1;    

}
.paper-shadow{
    box-shadow: 8px 8px 8px #cbced1;
}

.paper-anim{
 animation: paper-fly 2s forwards cubic-bezier(0.04,0.76,0.28,-0.1) ;
 animation-iteration-count: 1;
}

.paper-down-anim{
    animation:paper-down 1.2s forwards linear;
    animation-iteration-count: 1;
}

.paper-hide{
    opacity:0;
}

