    height:100%;
    zoom: 0.85;
    transform: scale(0.85);
    -webkit-transform:scale(0.85);
    -moz-transform:scale(0.85);
    -o-transform:scale(0.85);
    -ms-transform:scale(0.85);
    /* border:5px solid green; */
    /* src: ; */
    /* background-image: url(); */

}

.resting-paper{
    transform:rotateZ(14deg) rotateY(-49deg) rotateX(51deg) translateX(-89px) translateY(175px) translateZ(-24px) scaleX(1) scaleY(1);
    box-shadow: 8px 8px 8px #cbced1;
}

@keyframes print-anim{
    0%{transform:rotateZ(14deg) rotateY(-48deg) rotateX(54deg) translateX(-99px) translateY(-82px) translateZ(0px) scaleX(1) scaleY(1);}
    10%{transform:rotateZ(14deg) rotateY(-48deg) rotateX(54deg) translateX(-99px) translateY(-55px) translateZ(0px) scaleX(1) scaleY(1);}
    25%{transform:rotateZ(14deg) rotateY(-48deg) rotateX(54deg) translateX(-99px) translateY(-26px) translateZ(0px) scaleX(1) scaleY(1);}
    35%{transform:rotateZ(13deg) rotateY(-45deg) rotateX(52deg) translateX(-99px) translateY(15px) translateZ(0px) scaleX(1) scaleY(1);}
    45%{transform:rotateZ(13deg) rotateY(-45deg) rotateX(52deg) translateX(-99px) translateY(58px) translateZ(0px) scaleX(1) scaleY(1);}
    55%{transform:rotateZ(13deg) rotateY(-45deg) rotateX(52deg) translateX(-89px) translateY(95px) translateZ(0px) scaleX(1) scaleY(1);}
    70%{transform:rotateZ(13deg) rotateY(-45deg) rotateX(52deg) translateX(-89px) translateY(215px) translateZ(0px) scaleX(1) scaleY(1);}
    78%{transform:rotateZ(13deg) rotateY(-45deg) rotateX(52deg) translateX(-89px) translateY(164px) translateZ(-9px) scaleX(1) scaleY(1);}
    88%{transform:rotateZ(13deg) rotateY(-45deg) rotateX(52deg) translateX(-89px) translateY(188px) translateZ(-21px) scaleX(1) scaleY(1);}
    100%{transform:rotateZ(14deg) rotateY(-49deg) rotateX(51deg) translateX(-89px) translateY(175px) translateZ(-24px) scaleX(1) scaleY(1);}
}

@keyframes paper-fly{
    0%{transform: rotateZ(15deg) rotateY(-44deg) rotateX(52deg) translateX(200px);}
    30%{transform: rotateZ(23deg) rotateY(50deg) rotateX(11deg) translateX(256px) translateY(-60px);}
    100%{transform: rotateZ(15deg) rotateY(50deg) rotateX(-2deg) translateX(-28px) translateY(-245px);}
}
/*225, 200 ,156,110*/

@keyframes paper-down{
    0%{transform: rotateZ(15deg) rotateY(50deg) rotateX(-2deg) translateX(-28px) translateY(-245px);}
    25%{transform: rotateZ(15deg) rotateY(50deg) rotateX(-2deg) translateX(-28px) translateY(-225px);}
    50%{transform: rotateZ(15deg) rotateY(50deg) rotateX(-2deg) translateX(-28px) translateY(-200px);}
    75%{transform: rotateZ(15deg) rotateY(50deg) rotateX(-2deg) translateX(-28px) translateY(-156px);}
    100%{transform: rotateZ(15deg) rotateY(50deg) rotateX(-2deg) translateX(-28px) translateY(-110px);}

}