.container{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(133,2,97,1) 38%, rgba(178,0,232,1) 58%, rgba(0,19,40,1) 100%);
}

.frame-container{
    width:100%;
    height:100vmin;
    display: flex;
    align-items: center;
    justify-content: center;
    perspective: 50px;
    animation: zoom 10s ease-in-out infinite alternate;
}

p{
    color:#eb6b21;
}

.frame{
    position: absolute;
    width:70vmin;
    height:70vmin;
    border:5px solid cyan;
    border-radius: 25px;
    box-shadow:
     0px 0px 20px #00ccff,
    inset 0px 0px 20px #00ccff,
    0px 0px 40px #86066b,
    inset 0px 0px 40px #86066b,
    0px 0px 60px #eb6b21,
    inset 0px 0px 60px #eb6b21;
    filter: brightness(1.5) contrast(2);
    animation: breathe 5s ease-in-out infinite alternate;
    
}

@keyframes rotate{
    0%{transform:rotate(20deg) translateY(75px) translateZ(-30px);}
    50%{transform:rotate(-45deg) translateY(75px) translateZ(-30px);}
    100%{transform:rotate(20deg) translateY(75px) translateZ(-30px);}
}

.frame:nth-child(1){
    transform: rotate(20deg) translateY(0px) translateZ(0px);
    opacity: 1.00;
    animation-delay: 0s;
}
.frame:nth-child(2){
    transform: rotate(20deg) translateY(25px) translateZ(-10px);
    opacity: 0.90;
    animation-delay: 0.3s;
}
.frame:nth-child(3){
    transform: rotate(20deg) translateY(50px) translateZ(-20px);
    opacity: 0.80;
    animation-delay: 0.6s;
}
.frame:nth-child(4){
    transform: rotate(20deg) translateY(75px) translateZ(-30px);
    opacity: 0.60;
    animation-delay: 0.9s;
}
.frame:nth-child(5){
    transform: rotate(20deg) translateY(100px) translateZ(-40px);
    opacity: 0.50;
    animation-delay: 1.2s;
}
.frame:nth-child(6){
    transform: rotate(20deg) translateY(125px) translateZ(-50px);
    opacity: 0.40;
    animation-delay: 1.5s;
}
.frame:nth-child(7){
    transform: rotate(20deg) translateY(150px) translateZ(-60px);
    opacity: 0.30;
    animation-delay: 1.8s;
}
.frame:nth-child(8){
    transform: rotate(20deg) translateY(175px) translateZ(-70px);
    opacity: 0.20;
    animation-delay: 2.1s;
}

.text-box{
    position:absolute;
    top:0;
    left:0;
    font-size:5rem;
    color:#aefcf2;
}

.first{
    top:18%;
    margin:0 0 0 50px;
    width:450px;
    text-align: center;
    
}

.second{
    font-size:3rem;
    top:75%;
    left:67%;
    
}

@keyframes breathe{
    0%{filter: brightness(1.5) contrast(2);}
    50%{filter: brightness(1.1) contrast(1.8);}
    100%{filter: brightness(1.5) contrast(2);}
}

@keyframes zoom{
    0%{perspective: 45px;}
    50%{perspective: 55px;}
    100%{perspective: 50px;}
}