*{
    color:red;
}

.body-container{
    width:100vw;
    height: 100%;
    overflow: hidden;
    position: absolute;
}

.bg-container{
    position: absolute; /* abs and 100% for fullscreen here*/
    width:100vw;
    height: 100%;
    /* border:1px solid green; */
    display:flex;
    flex-direction: row;
    flex-wrap: nowrap;
    
    overflow: hidden;
    z-index:1;
    
}

.bg{
    position: absolute; /* abs and 100% for fullscreen here*/
    width:200vw;
    height: 100%;
    filter: blur(10px) contrast(2) brightness(1.5);
background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(152,175,3,1) 17%, rgba(133,2,97,1) 32%, rgba(1,149,154,1) 43%, rgba(178,0,232,1) 58%, rgba(191,4,169,1) 67%, rgba(48,133,9,1) 77%, rgba(189,116,17,1) 87%, rgba(7,76,152,1) 100%);
    z-index: 0;
    animation:bg-anim 2s linear infinite alternate;
    transform: translateX(-50%) translateY(0%);
}

.curtain{
    /* border: 1px solid black; */
    margin:0 0.5px 0 0.5px;
    background:linear-gradient(0deg,black,white,white,white,black);   
    width:20%;
    height:inherit;
    transition:transform ease-in 0.4s;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;    
    z-index: 1;
    
}

.curtain:nth-child(1){
    margin:0 0.5px 0 0;
}
.curtain:nth-child(7){
    margin:0 0 0 0.5px;
}

.curtain:hover{
    /* transform: skewY(-10deg) rotateY(15deg); */
    transform: translateX(-1px);
    /* transform: rotateY(35deg); */
}

.curtain-anim{
    animation: anim 3s cubic-bezier(.99,-0.13,1,.9) 1 forwards;
    transform: skewY(-20deg);
}

.welcome{
    margin:0 0 40% 0;
}

.click{
    text-align: center;    
}

:root{
    --opacity:0;
    --c1:'Hi, I am Mohit';
    --c2:'he/him';
    --c3:'front-end';
    --c4:'middleware';
    --c5:'back-end.';
    --c6:'designer.';
    --c7:'developer.';
}

.curtain-grid{
    margin:0.5px 0.45px 0.45px 0.45px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    transition:transform ease-in 0.4s;
    padding:0;
}

.curtain-grid:hover{
    transform: translateX(-1px);
}

.curtain-grid:nth-child(1){
    grid-column-start:1;
    grid-column-end: 8;
    grid-row-start: 1;
    grid-row-end: 2;
    grid-row:span 1;
    margin-bottom:0.3px;

    background: linear-gradient(90deg, rgba(255,150,144,1) 0%, rgba(255,105,97,1) 100%);
}

.curtain-grid:nth-child(2){
    grid-row-start: 2;
    grid-row-end:4;
    
    background: linear-gradient(90deg,  rgba(255,150,144,1) 0%, rgba(255,137,130,1) 100%);
}

.curtain-grid:nth-child(3){
    grid-row-start: 2;
    grid-row-end:3;
    background: linear-gradient(90deg,  rgba(255,137,130,1) 0%, rgba(255,130,123,1) 100%);
}

.curtain-grid:nth-child(4){
    grid-row-start: 2;
    grid-row-end:3;
    grid-column: span 2;
    background: linear-gradient(90deg,  rgba(255,130,123,1) 0%, rgba(255,115,108,1) 100%);
}

.curtain-grid:nth-child(5){
    grid-row-start: 2;
    grid-row-end:3;
    background: linear-gradient(90deg,  rgba(255,115,108,1) 0%,#ff6f68 100%);
}

.curtain-grid:nth-child(6){
    grid-row-start: 2;
    grid-row-end:4;
    grid-column: span 2;
    background: linear-gradient(90deg,  rgba(255,115,108,1) 0%, rgba(255,105,97,1) 100%);
}

.curtain-grid:nth-child(7){
    grid-row-start: 3;
    grid-row-end:4;
    grid-column: span 4;
    margin-top:0.5px;
    background: linear-gradient(90deg, rgba(255,137,130,1) 0%,   rgba(255,115,108,1) 100%);
}

.curtain-grid:before{
    position: absolute;
    /* top:101%; */
    left:0;
    width:100%;
    height: 100%;
    content:'hello';
    display: flex;
    align-items: center;
    justify-content: center;
    color:red;
    font-size: 2rem;
    /* border:1px solid red; */
    animation:text-anim 2.2s forwards cubic-bezier(0.68, -0.55, 0.265, 1.55) 1;
    opacity:var(--opacity);
}

.curtain-grid:nth-child(1):before{
    animation-delay: -0.3s;
    content:var(--c1);
}
.curtain-grid:nth-child(2):before{
    animation-delay: -0.1s;
    content:var(--c2);
}
.curtain-grid:nth-child(3):before{
    animation-delay: 0.0s;
    content:var(--c3);
}
.curtain-grid:nth-child(4):before{
    animation-delay: 0.5s;
    content:var(--c4);
}
.curtain-grid:nth-child(5):before{
    animation-delay: 0.8s;
    content:var(--c5);
}
.curtain-grid:nth-child(6):before{
    animation-delay: 1.0s;
    content:var(--c6);
}
.curtain-grid:nth-child(7):before{
    animation-delay: 1.3s;
    content:var(--c7);
}

.grid-click-mask{
    opacity:0;
}

@keyframes text-anim{
    0%{
        --opacity:1;top:101%;}
    100%{
        --opacity:1;top:0%;}
}

.grid-click{
    animation: grid-click-anim 1.5s cubic-bezier(0.68, -0.55, 0.265, 1.55) forwards 1;
}

.grid-click-text{
    color:red;
    font-size: 2rem;
    /* display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; */
    text-align: center;
    padding: 0;
}

.grid-click-text-ini{
    color:red;
    font-size: 2rem;
    /* display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; */
    text-align: center;
    padding: 0;
}

@keyframes grid-click-anim{
    0%{transform: rotateY(0deg);}
    100%{transform: rotateY(360deg);}
}

.bg-container-grid{
    display: grid;
    gap:0.5px;
    position: absolute;
    width:100vw;
    height: 100%;
    overflow: hidden;
    z-index:1;
    /* grid-auto-flow:column; */
    grid-template-columns:repeat(7,1fr);
    /* border: 1px solid green; */
}


@keyframes anim{
    20%{transform:skewY(-10deg) rotateY(35deg);}
    100%{transform: skewY(-20deg) rotateY(2160deg);}
/* 60%{transform: skewY(-20deg) rotateY(720deg)}
100%{transform: skewY(-20deg) rotateY(2160deg)} */
}

@keyframes bg-anim{
    0%{transform: translateX(0%) translateY(0%)};
    100%{transform: translateX(40%) translateY(20%)};
}
