.effect{
    height: 100vmin;    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top:-10%;
    perspective: 1000px;
    animation:effect 2.5s ease-in-out alternate 1;
    border:1px solid black;
    background-color: black;
    height:140vh;
    z-index:-1;
    position:relative;
}

.effect > div{
    transform-style: preserve-3d;
    font-size: 3rem;
    transform-origin: center;
    /* transform:rotateX(330deg) translateZ(200px); */
    transform:translateZ(200px); 


}



@keyframes effect{
    0%{perspective: 175px;}
    100%{
        perspective:1000px;
        transform:translateZ(0px);
    }
}



.logo > g > path{
    animation: animate 5s ease-in infinite alternate;
}

.logo >g > path:nth-child(1){
    stroke-dasharray: 530px;
    stroke-dashoffset: 530px;
    animation-delay: 0s;
}

.logo >g > path:nth-child(2){
    stroke-dasharray: 593.47px;
    stroke-dashoffset: 593.47px;
    animation-delay: 0.3s;
}

.logo >g > path:nth-child(3){
    stroke-dasharray: 631.74px;
    stroke-dashoffset: 631.74px;
    animation-delay: 0.6s;
}

.logo >g > path:nth-child(4){
    stroke-dasharray: 708px;
    stroke-dashoffset: 708px;
    animation-delay: 0.9s;
}

@keyframes animate{
    to{
        stroke-dashoffset: 0;
        stroke-dasharray: 0;
    }
}