*{
    font-family: 'Comfortaa',cursive;
    font-weight: 400;
    --light-font:#def;
    --dark-font:#666;
    color:var(--light-font);
}

.navbar-ul{
    display:flex;
    flex-direction: row;
    justify-content: space-evenly;
    padding:20px 0 20px 0;
    /* border:1px solid black; */
    z-index:2;
    position:absolute;
    top:0;
    left:0;
    width:100vw;
    
}

.navbar-li{
    list-style-type: none;
    margin:0 10px 0 0;
    padding:5px 10px;
    position: relative;
    /* border:1px dashed black; */
    overflow: hidden;
    color:#def;
}

