:root{
    --op1:0;
    --op2:0;
    --op3:0;
    --op4:0;
    --op5:0;
    --op6:0;
    --dur1:0s;
    --dur2:0s;
    --dur3:0s;
    --dur4:0s;
    --dur5:0s;
    --dur6:0s;
}
.navbar-li:after{
    position: absolute;
    content:'Home';
    /* animation:anim-2 4s infinite linear; */
    cursor: pointer;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    color:gold;
    background: transparent;
    /* text-align: center; */
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Comfortaa',cursive;
    font-weight: bolder;
    
}

.anim{
    animation:anim-2 4s infinite linear;
}

.navbar-li:nth-child(1):after{
    content:'Home';
    /* visibility: var(--v1); */
    opacity:var(--op1);
    animation:anim-2 var(--dur1) infinite linear;
}
.navbar-li:nth-child(2):after{
    content:'Design';
    /* visibility: var(--v2); */
    opacity:var(--op2);
    animation:anim-2 var(--dur2) infinite linear;
}
/* .navbar-li:nth-child(3):after{
    content:'Page3';
    /* visibility: var(--v3); */
    /* opacity:var(--op3);
    animation:anim-2 var(--dur3) infinite linear;
}  */
.navbar-li:nth-child(3):after{
    content:'Printer';
    /* visibility: var(--v4); */
    opacity:var(--op3);
    animation:anim-2 var(--dur3) infinite linear;
}
.navbar-li:nth-child(4):after{
    content:'Contact';
    /* visibility: var(--v5); */
    opacity:var(--op4);
    animation:anim-2 var(--dur4) infinite linear;
}
.navbar-li:nth-child(5):after{
    content:'Resume';
    /* visibility: var(--v6); */
    opacity:var(--op5);
    animation:anim-2 var(--dur5) infinite linear;
}

.res-link{
    text-decoration: none;
    color:var(--light-font);
}

@keyframes anim-2{
    0%{clip-path: polygon(15% 11%, 100% 14%, 98% 10%, 9% 100%);}
    20%{clip-path: polygon(10% 51%, 100% 34%, 89% 70%, 20% 10%);}
    40%{clip-path: polygon(20% 51%, 60% 19%, 40% 13%, 91% 40%);}
    60%{clip-path: polygon(22% 1%, 10% 4%, 86% 10%, 9% 10%);}
    80%{clip-path: polygon(40% 21%, 29% 42%, 28% 90%, 99% 80%);}
    100%{clip-path: polygon(15% 20%, 90% 14%, 91% 10%, 30% 90%);}
}