.text-container{
    background-color:black;
    height:100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo > g > path{
    animation: animate 5s ease-in infinite alternate;
}

.logo >g > path:nth-child(1){
    stroke-dasharray: 325.82x;
    stroke-dashoffset: 325.82px;
    animation: animate 5s ease-in infinite alternate;
    animation-delay: 0s;
}

.logo >g > path:nth-child(2){
    stroke-dasharray: 418.17px;
    stroke-dashoffset: 418.17px;
    animation: animate 5s ease-in infinite alternate;
    animation-delay: 0.3s;
}

.logo >g > path:nth-child(3){
    stroke-dasharray: 493.25px;
    stroke-dashoffset: 493.25px;
    animation: animate 5s ease-in infinite alternate;
    animation-delay: 0.6s;
}

.logo >g > path:nth-child(4){
    stroke-dasharray: 519.73px;
    stroke-dashoffset: 519.73px;
    animation: animate 5s ease-in infinite alternate;
    animation-delay: 0.9s;
}

.logo >g > path:nth-child(5){
    stroke-dasharray: 369.30px;
    stroke-dashoffset: 369.30px;
    animation: animate 5s ease-in infinite alternate;
    animation-delay: 1.1s;
}

.logo >g > path:nth-child(6){
    stroke-dasharray: 406.30px;
    stroke-dashoffset: 406.30px;
    animation: animate 5s ease-in infinite alternate;
    animation-delay: 1.3s;
}

.logo >g > path:nth-child(7){
    stroke-dasharray: 594.30px;
    stroke-dashoffset: 594.30px;
    animation: animate 5s ease-in infinite alternate;
    animation-delay: 1.5s;
}
 
.logo >g > path:nth-child(8){
    stroke-dasharray: 45px;
    stroke-dashoffset: 45px;
    animation: loaders 5s ease-in infinite forwards;
    animation-delay: 0.1s;
}

.logo >g > path:nth-child(9){
    stroke-dasharray: 45px;
    stroke-dashoffset: 45px;
    animation: loaders 5s ease-in infinite forwards;
    animation-delay: 0.5s;
}

.logo >g > path:nth-child(10){
    stroke-dasharray: 45px;
    stroke-dashoffset: 45px;
    animation: loaders 5s ease-in infinite forwards;
    animation-delay: 1s;
}

@keyframes animate{
    50%{
        stroke-dashoffset: 300px;
        stroke-dasharray: 30px;
    }
    100%{
        stroke-dashoffset: 0;
        stroke-dasharray: 0;
    }
}

@keyframes loaders{
    50%{
        stroke-dashoffset: 0;
        stroke-dasharray: 0;
    }
    100%{
        stroke-dashoffset: 45px;
        stroke-dasharray: 45px;       
    }
}